import {
  Backdrop,
  Button,
  CircularProgress,
  Grid,
  Typography
} from "@material-ui/core";
import React from "react";
import OptionCard from "../../Common/OptionCard/OptionCard";
import HotspotConnect from "../NetworkType/HotspotConnect";
import { setStorage, getStorage, fillStorage } from "../../../Utility/helpers";
import WifiConnect from "../NetworkType/WifiConnect";
import {
  ArrowForward,
  Language,
  SettingsInputHdmi,
  SettingsRemote
} from "@material-ui/icons";

class NetworkSelect extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      stage: "internet-enable",
      enableInternet: false,
      selectedInterface: null,
      enableHotspot: false,
      connecting: false
    };
  }

  componentDidMount() {
    if(getStorage("initialUpdate") === 1) {
      setStorage("initialUpdate", 2);
      this.props.setStep(2);
    }
    if((getStorage("networkProfile") && getStorage("networkProfile") !== 'none') && getStorage("internet")) {
      !this.props.settingsScreen && this.props.setStep(1);
    }
  }

  complete() {

    setStorage("hotspot", this.state.enableHotspot);
    setStorage("internet", this.state.enableInternet);
    setStorage("networkProfile", this.state.selectedInterface);

    if (this.props.setNewType) {
      this.props.setNewType(this.state.selectedInterface);
      this.props.setStep(0);
    } else {
      if (this.state.enableInternet) {
        this.props.setStep(1);
      } else {
        this.props.setStep(2);
      }
    }

  }

  render() {

    async function connect(address, callback) {
      // Default options are marked with *
      const response = await fetch(address, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      }).then((data) => {
        callback();
      }).catch((err) => {
          setTimeout(() => {
            connect(address, callback);
          }, 1000);
      });
    }

    if (this.state.stage === "internet-enable") {
      return (
        <>
          {this.renderHeader()}
          <Language
            style={{
              fontSize: 100,
              width: "100%",
              textAlign: "center",
              marginBottom: 20
            }}
          />
          <Typography variant="h5">
            Would you like to connect this printer to the internet?
          </Typography>

          <Typography variant="h6">
            Connecting to the internet enables updates.
          </Typography>
          <OptionCard
            title="Yes"
            onClick={() => {
              this.setState({ enableInternet: true, stage: "internet-select" });
            }}
          ></OptionCard>
          <OptionCard
            title="No"
            onClick={() => {
              this.setState({
                enableInternet: false,
                stage: "hotspot-enable",
                selectedInterface: "none"
              });
            }}
          ></OptionCard>
        </>
      );
    }
    if (this.state.stage === "internet-select") {
      return (
        <>
          {this.renderHeader()}
          <Language
            style={{
              fontSize: 100,
              width: "100%",
              textAlign: "center",
              marginBottom: 20
            }}
          />
          <Typography variant="h5">
            How would you like to connect this printer?
          </Typography>
          <OptionCard
            title="Wi-Fi"
            onClick={() => {

              let hotspot = getStorage('hotspot');

              if(hotspot) {
                setStorage("hotSpotToWifi", true);
                this.setState({
                  selectedInterface: "wifi",
                  enableHotspot: false,
                  enableInternet: true,
                  stage: "wifi-select",
                });

                connect(`http://localhost:8990/hotspot/disconnect`, () => {
                  console.log("hotspot disconnected");
                });

              } else {
                this.setState({
                  selectedInterface: "wifi",
                  stage: "wifi-select"
                });
              }
            }}
          ></OptionCard>
          <OptionCard
            title="Ethernet"
            onClick={() => {
              this.setState({
                selectedInterface: "ethernet",
                stage: "ethernet-check"
              });
            }}
          ></OptionCard>
        </>
      );
    }
    if (this.state.stage === "ethernet-check") {
      return (
        <>
          {this.renderHeader()}
          <SettingsInputHdmi
            style={{
              fontSize: 100,
              width: "100%",
              textAlign: "center",
              marginBottom: 20
            }}
          />
          <Typography variant="h5">
            Please verify that the ethernet cable is properly connected to your
            device.
          </Typography>
          <OptionCard
            title="Done"
            onClick={() => {
              this.setState({ stage: "hotspot-enable" });
            }}
          ></OptionCard>
        </>
      );
    }
    if (this.state.stage === "wifi-select") {
      return (
        <>
          {this.renderHeader()}
          <WifiConnect
            setStep={(step) => {
              this.setState({connecting: true});
              let currentContext = this;

              setTimeout(() => {
                currentContext.setState({connecting: false});
                fillStorage().then(function() {
                  currentContext.props.setNetworkData(getStorage("network"));
                  currentContext.complete();
                });
              }, 15000);
            }}
            setShowSnack={this.props.setShowSnack}
            setSnackMessage={this.props.setSnackMessage}
            handleSnackClose={this.props.handleSnackClose}
            setConnectWifi={() => {}}
            onGoBack={() => {
              this.setState({ stage: "internet-select" });
            }}
          ></WifiConnect>
          {this.state.connecting && (
            <Backdrop style={{
              zIndex: 90000,
              color: "#fff"
            }} open={this.state.connecting}>
              <div style={{
                display: "flex",
                zIndex: 99000,
                flexDirection: "column",
                alignItems: "center",
                "& h4": {
                  marginBottom: "24px"
                }
              }}>
                <h4>Please Wait</h4>
                <CircularProgress color="inherit" />
              </div>
            </Backdrop>
          )}
        </>
      );
    }

    if (this.state.stage === "hotspot-enable") {
      return (
        <>
          {this.renderHeader()}
          <SettingsRemote
            style={{
              fontSize: 100,
              width: "100%",
              textAlign: "center",
              marginBottom: 20
            }}
          />
          <Typography variant="h5">
            Would you like to enable the printer's wireless hotspot?
          </Typography>
          <Typography variant="h6">
            This allows you to remotely connect/control your printer.
          </Typography>
          <OptionCard
            title="Yes"
            onClick={() => {
              this.setState({
                enableHotspot: true,
                stage: "config-hotspot",
                selectedInterface: "hotspot",
              });
              connect(`http://localhost:8990/hotspot/connect`, () => {
                console.log("hotspot connected");
              });

              setStorage("networkProfile", "hotspot");
              let network = getStorage("network");
              if(!network) {
                network = {};
              }
              network.ssid = '';
              network.ip = '192.168.50.5';
              network.hostName = 'http://'+getStorage("printerName")+'.local';
              setStorage("network", network);
            }}
          ></OptionCard>
          <OptionCard
            title="No"
            onClick={() => {
              this.complete();
            }}
          ></OptionCard>
        </>
      );
    }

    if (this.state.stage === "config-hotspot") {
      return (
        <>
        <HotspotConnect
          setStep={() => {
            this.setState({connecting: true});
            setTimeout(() => {
              this.setState({connecting: false});
              this.complete();
            }, 10000);
          }}
          setConnectWifi={() => {}}
          machineName={this.props.machineName}
        />
        {this.state.connecting && (
          <Backdrop style={{
            zIndex: 90000,
            color: "#fff"
          }} open={this.state.connecting}>
            <div style={{
              display: "flex",
              zIndex: 99000,
              flexDirection: "column",
              alignItems: "center",
              "& h4": {
                marginBottom: "24px"
              }
            }}>
              <h4>Please Wait</h4>
              <CircularProgress color="inherit" />
            </div>
          </Backdrop>
        )}
        </>
      );
    }
  }

  renderHeader() {
    return (
      <Grid
        container
        justify="space-between"
        alignItems="center"
        style={{ display: "flex", marginBottom: 20 }}
      >
        <h2 style={{ fontSize: 28 }}>Network</h2>
        <Button
          aria-label="skip"
          endIcon={<ArrowForward />}
          onClick={() => {
            this.setState(
              {
                enableInternet: getStorage('internet') ? getStorage('internet') : false,
                stage: "hotspot-enable",
                selectedInterface: getStorage('networkProfile') ? getStorage('networkProfile') : "none"
              },
              () => {
                this.complete();
              }
            );
          }}
        >
          Skip
        </Button>
      </Grid>
    );
  }
}

// setStep={setStep}
// setShowSnack={setShowSnack}
// setSnackMessage={setSnackMessage}
// handleSnackClose={handleSnackClose}
// setConnectWifi={setConnectWifi}
// />

export default NetworkSelect;
