import React from 'react';
import OptionCard from '../../Common/OptionCard/OptionCard';
import { getStorage, setStorage } from '../../../Utility/helpers';
import { useMountEffect } from "../../../Utility/useMountEffect";
import './UserLevel.css';
const UserLevel = ({ handleOnboarding, setStep, settingsScreen }) => {

  useMountEffect(() => {
    if(getStorage("didUserMode")) {
      !settingsScreen && setStep(5);
    }
  });

  const handleClick = async (val) => {
    let networkType = getStorage("networkProfile");
    setStorage('userMode', val);
    setStorage('onboarded', true);
    setStorage('didUserMode',true);
    if (networkType === 'wifi' || networkType === 'ethernet') {
      //check for updates
      setStep(5);
    } else {
      setStep(5);
      //handleOnboarding(val);
    }
  };
  return (!getStorage("didUserMode") && (
    <div className="menu-container">
      <h3>Please choose a mode</h3>
      <div className="menu-options-container">
        <OptionCard
          title="Beginner"
          description="For beginners. This mode has limited slicer settings."
          onClick={() => handleClick('beginner')}
        />
        <OptionCard
          title="Advanced"
          description="For users familiar with 3D printing. Additional settings unlocked."
          onClick={() => handleClick('advanced')}
        />
        <OptionCard
          title="Expert"
          description="For expert users. All slicer settings unlocked."
          onClick={() => handleClick('expert')}
        />
      </div>
    </div>
  ));
};

export default UserLevel;
