import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Checkbox,
  Divider,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListItemSecondaryAction,
  Paper,
  Typography,
  Slider,
  Button,
  IconButton,
  FormGroup,
  FormControlLabel,
  FormControl
} from "@material-ui/core";
import {
  Add,
  DeleteForever,
  Description,
  ExpandMore
} from "@material-ui/icons";
import React, { useContext, useState } from 'react';
import AppContext from "../../../../AppContext";
import * as THREE from "three";
import STLLoader from "../RenderingUtils/STLLoader";
import { useMountEffect } from "../../../../Utility/useMountEffect";

import "./style.css";

async function getFile(fileName) {
  // await response of fetch call
  let response = await fetch(`http://localhost:9990/${fileName}`);
  // only proceed once promise is resolved
  let data = await response.blob();
  // only proceed once second promise is resolved
  return data;
}

function FilesPanel() {
  const context = useContext(AppContext)

  let fileName = null;

  if(context.stl) {
    if(typeof context.stl == "string") {
      fileName = context.stl;
    } else {
      fileName = context.stl.name;
    }
  }

  const [listOpen, setListOpen] = React.useState(true);
  const [fileList, setFileList] = React.useState([]);
  const [uploadElement, setUploadElement] = useState(null);
  const handleListItemClick = (event, currentFile) => {
    context.setSelectedFile(currentFile.name);
  };
  const swapTrasformTool = (e, file) => {
    for (let idx in context.objRoot.children) {
      let mesh = context.objRoot.children[idx];
      if(mesh.name === file.name) {
        context.control.attach(mesh);
      }
    }
  };

  const deleteFile = (file) => {
    console.log('deleting '+file.name)
    for (let idx in context.objRoot.children) {
      let mesh = context.objRoot.children[idx];
      if(mesh.name === file.name) {
        context.objRoot.remove(mesh);
      }
    }

    let filesWithoutDeleted = fileList.filter(function( obj ) {
    return obj.name !== file.name;
    });

    if(filesWithoutDeleted) {
      setFileList(filesWithoutDeleted);
    }
  };

  useMountEffect(() => {
    if(context.stl) {
      if(typeof context.stl == "string") {
        getFile(fileName).then(function(data) {
          let file = new File([data], fileName);
          setFileList([{
              'name': fileName,
              'file': file
            }]
          );
        });
      } else {
        setFileList([{
          'name': fileName,
          'file': context.stl
        }]);
      }
    }

  });

  return (
    <>
      <div className="files-panel">
          <AppContext.Consumer>
            {() => (
              <>
              <Accordion expanded={listOpen} onChange={() => setListOpen(!listOpen)}>
                <AccordionSummary
                  expandIcon={<ExpandMore />}
                  aria-controls="panel1a-content"
                  id="panel1a-header"
                >
                  <Typography>
                  Object List
                  </Typography>
                </AccordionSummary>
                <AccordionDetails style={{justifyContent:"center"}}>
                <Grid container direction="column" item xs>
                  {fileList.length > 0 && (
                    <Grid item xs>
                    <List aria-label="file-list" style={{width:'100%'}}>
                    {fileList.length > 0 && fileList.map((currentFile, index) => {
                      return (
                        <ListItem
                          button
                          selected={context.selectedFile === currentFile.name}
                          onClick={(event) => handleListItemClick(event, currentFile)}
                          style={{backgroundColor:'#b7b7b7'}}
                          key={index}
                        >
                          <ListItemIcon>
                            <Checkbox
                              checked={context.selectedFile === currentFile.name}
                              onChange={ (e) => swapTrasformTool(e, currentFile)}
                              color="primary"
                              inputProps={{ 'aria-label': 'secondary checkbox' }}
                            />
                          </ListItemIcon>
                          <ListItemText primary={currentFile.name} />
                        </ListItem>
                      )
                    })}
                    </List>
                    </Grid>
                  )}
                  </Grid>
                </AccordionDetails>
              </Accordion>
              </>
            )}
          </AppContext.Consumer>
      </div>
    </>
  );
}

export default FilesPanel;
